import React from 'react';
import { Link } from 'gatsby';
import styled from 'styled-components';
import { GatsbyImage } from 'gatsby-plugin-image';

const Wrapper = styled.li`
    width: 100%;
    position: relative;
    display: block;
    height: 12vw;
    width: 100%;
    overflow: hidden;

    @media (min-width: 769px) {
        width: 12vw;

        &:before {
            content: 'title';
            position: absolute;
            z-index: 100;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: white;
            opacity: 0;
        }
        &:hover:before {
            opacity: 1;
        }
    }
`;

const ProjectLink = ({ image, slug }) => {
    return (
        <Wrapper>
            <Link
                to={slug}
                style={{
                    width: '100%',
                    height: '100%',
                }}>
                <GatsbyImage
                    image={image}
                    alt='image'
                    style={{
                        width: '100%',
                        height: '100%',
                    }}
                />
            </Link>
        </Wrapper>
    );
};

export default ProjectLink;
